.loader-container {
  z-index: 1055;
  text-align: center;
  position: absolute;
  top: 15%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.loader-hidden {
  display: none;
}

.download-progress-elems {
  width: max-content;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

@media only screen and (device-width >= 320px) and (device-width <= 355px) and (-webkit-device-pixel-ratio >= 2) and (orientation: portrait) {
  .loader-container {
    top: 20%;
  }

  .download-progress-elems {
    top: 80%;
  }
}
